.skills__slider {
    position: relative;
    display: flex;
    padding: 0 30px;
}

.skills__slide {
    display: flex;
    justify-content: center;
    align-items: center;
    // margin: 0 10px;
    padding: 0 20px;

    @media screen and (max-width: $Medium-l) {
        margin: 0 10px;
    };
    @media screen and (max-width: $Medium) {
        margin: 0;
    };
}

.slick-list {
    overflow: hidden;
}

.slick-track {
    display: flex;
}

.slick-arrow {
    position: absolute;
    top: 50%;
    border: none;
    background: transparent;
    font-size: 0;
    z-index: 5;

    &.slick-prev::before,&.slick-next::before {
        content: '';
        position: absolute;
        top: 50%;
        transform: translateY(-10px) rotate(45deg);//повернул обе стрелки в одну сторону 
        width: 20px;                               //но через border раскрасил разные стороны
        height: 20px;
    }

    &.slick-prev {
        left: 1%;

        &::before {
            border-left: 5px solid $white;
            border-bottom: 5px solid $white;
        }
    }

    &.slick-next {
        left: 99%;
        transform: translateX(-20px);

        &::before {
            border-right: 5px solid $white;
            border-top: 5px solid $white;
        }
    }

    &.slick-prev,&.slick-next {
        @media screen and (max-width: 768px) {
        display: none;
    };
    }
}


.skills {
    background: #F9F9F9;
    &__slider {
        display: grid;
        grid-template-rows: repeat(2, 1fr);
        grid-template-columns: repeat(5, 1fr);
        // row-gap: 100px;
        row-gap: 2%;

        @media screen and (max-width: $Medium) {
            grid-template-rows: repeat(4, 1fr);
            grid-template-columns: repeat(6, 1fr);
            // row-gap: 50px;
        };

        @media screen and (max-width: $Small-l) {
            // row-gap: 25px;
        };
    }


    &__slide {
        @media screen and (max-width: $Medium) {
            &:nth-child(1) {
                grid-row: 1/2;
                grid-column: 1/3;
            }
            &:nth-child(2) {
                grid-row: 1/2;
                grid-column: 3/5;
            }
            &:nth-child(3) {
                grid-row: 1/2;
                grid-column: 5/7;
            }
            &:nth-child(4) {
                grid-row: 2/3;
                grid-column: 2/4;
            }
            &:nth-child(5) {
                grid-row: 2/3;
                grid-column: 4/6;
            }
            &:nth-child(6) {
                grid-row: 3/4;
                grid-column: 1/3;
            }
            &:nth-child(7) {
                grid-row: 3/4;
                grid-column: 3/5;
            }
            &:nth-child(8) {
                grid-row: 3/4;
                grid-column: 5/7;
            }
            &:nth-child(9) {
                grid-row: 4/5;
                grid-column: 2/4;
            }
            &:nth-child(10) {
                grid-row: 4/5;
                grid-column: 4/6;
            }
        }

        & img {
            margin: 20px;

            @media screen and (max-width: $Medium) {
                margin: 0 auto;
            };
        }
    }
}